<template>
  <div id="header">
    <div class="navbar">
      <div class="title"><span>考拉智能监控管理系统</span></div>
      <el-dropdown class="exist"  @command="handleCommand">
        <span class="el-dropdown-link">
          下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="exist" divided>退出系统</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return{

    }
  },
  methods: {
    handleCommand(val) {
      if(val === 'exist' && this.$route.query.type === '0') {
        this.$router.push('/workbench/workbench')
      } else {
        this.$router.push('/monitoringLogin')
      }
    }
  }
};
</script>
<style scoped>
  .navbar{
    height: 60px;
    width: 100%;
    /* min-width: 1650px; */
    background: #2B333E;
    color: #FFFFFF;
    font-family: PingFang-SC-Bold;
    font-weight:bold;
  }
  .title{
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    height: 60px;
    line-height: 60px;
    font-size:24px;
  }
  .title:after{
  content:'';
  width:0;
  height:100%;
  display:inline-block;
  vertical-align:middle;
}
.exist{
    float: right;
    color: #fff;
    margin-top: 20px;
    vertical-align: middle;
  }

</style>





