<template>
  <div id="side">
    <el-menu
      :default-active="$route.path"
      class="el-menu-vertical-demo"
      background-color="#2B333E"
      active-text-color="#fff"
      text-color="#A8B1BA"
      :collapse="isCollapse"
      router
    >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">智能安防管理</span>
        </template>
        <el-menu-item index="/monitoringSystem/monitoring_system/deviceManage" >设备管理</el-menu-item>
        <el-menu-item index="/monitoringSystem/monitoring_system/faceControl" >人脸库管理</el-menu-item>
        <el-menu-item index="/monitoringSystem/monitoring_system/eventManage">事件管理</el-menu-item>
        <el-menu-item index="/monitoringSystem/monitoring_system/monitoringRecord" >监控记录</el-menu-item>
        <el-menu-item index="/monitoringSystem/monitoring_system/warning">告警</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>
<script>

export default {
  data() {
    return {
      isCollapse: false,
      screenWidth: document.body.clientWidth,
    };
  },
  mounted() {
    // console.log(document.body.clientWidth)
    if(document.body.clientWidth <= 1290) {
      this.isCollapse = true;
    }else {
      this.isCollapse = false;
    }
    const that = this
    window.onresize = () => {
      return (() => {
        // window.screenWidth = document.body.clientWidth
        // that.screenWidth = window.screenWidth
        that.screenWidth = document.body.clientWidth
      })()
    }
  },
  methods: {
    initStyle() {
      if(this.screenWidth <= 1290) {
        this.isCollapse = true;
      }else {
        this.isCollapse = false;
      }
    }
  },
  watch: {
    screenWidth(newVal) {
      if(!this.timer) {
        this.screenWidth = newVal
        this.timer = true
        setTimeout(() => {
          this.initStyle();
          this.timer = false
        }, 400)
      }
    }
  },
};
</script>
<style lang="scss">
#side{
  width: 100%;
  .el-menu{
    border-right: none;
  }
  .el-menu-vertical-demo {
    width: 100%;
  }
  .el-menu-item{
    min-width: 0;
    padding-left: 60px !important;
  }
  .is-active{
    background: #368FFF !important;
  }
  .el-menu{
    border-right-width: 0;
  }
  .el-icon-location{
    margin-right: 0;
    width: 18px;
    @media screen and(max-width:1290px){
      margin-left: 0;
    }
  }
}
</style>



