<template>
  <div id='main'>
    <el-container class="content-box">
      <el-header>
        <klHeader>
        </klHeader>
      </el-header>
      <el-container class="main-content">
        <el-aside class="aside" width="212px">
        <klSide></klSide>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import klHeader from "./header.vue";
import klSide from './side'
export default {
  components: {
    klHeader: klHeader,
    klSide: klSide
  }
}
</script>

<style lang="scss" scoped>
#main{
  width: 100%;
  height: 100%;
}
.content-box{
  width: 100%;
  height: 100%;
  .el-header {
    background-color: white;
    box-shadow: 0px 2px 10px 0px rgba(211, 211, 211, 1);
    z-index: 99;
    background-color:#2B333E;
    // min-width: 1366px;
    min-width: 1024px;
  }
  .main-content{
    min-height: 780px;
    .aside {
      // width: 212px !important;
      background-color:#2B333E;
      box-shadow: 0px 2px 10px 0px rgba(211, 211, 211, 1);
      @media screen and(max-width:1290px){
        width: 64px !important;
        transition: all .5s;
      }
    }
    .el-main {
      position: relative;
      background: #F5F8FC;
      // min-width: 960px;
      // width: 1154px;
    }
  }
}


</style>



